<template>
  <div class="ele-upload-video">
    <!--  -->
    <el-upload
      :accept="accept"
      :action="action"
      :before-upload="handleBeforeUploadVideo"
      :data="data"
      :disabled="videoUploadPercent > 0 && videoUploadPercent < 100"
      :headers="headers"
      :httpRequest="httpRequest"
      :name="name"
      :on-error="handleUploadError"
      :on-progress="handleUploadProcess"
      :on-success="handleUploadSuccess"
      :show-file-list="false"
      :withCredentials="withCredentials"
      drag
      v-if="!value"
    >
      <!--  -->
      <el-progress
        :percentage="videoUploadPercent"
        style="margin-top: 20px;"
        type="circle"
        v-if="videoUploadPercent > 0"
      />

      <!--  -->
      <template v-else>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          
          <em></em>
        </div>
        <div class="el-upload__tip" slot="tip" v-if="showTip">
          
          <span style="color: #f56c6c;"
            >&nbsp;{{
              this.fileType ? this.fileType.join("/") : ""
            }}&nbsp;</span
          >
          <template v-if="fileSize">
            
            <span style="color: #f56c6c;">{{ fileSize }}</span
            >&nbsp;MB
          </template>
        </div>
      </template>
    </el-upload>

    <!--  -->
    <vue-hover-mask v-if="value">
      <video
        :autoplay="false"
        :src="value"
        :style="{
          width: width + 'px',
          height: height ? height + 'px' : 'auto',
        }"
      >
        
      </video>
      <template v-slot:action>
        <span @click="handlePlayerVideo" class="ele-upload-video_mask__item">
          <i class="el-icon-zoom-in"></i>
        </span>
        <span @click="handleRemove" class="ele-upload-video_mask__item">
          <i class="el-icon-delete"></i>
        </span>
      </template>
    </vue-hover-mask>

    <!--  -->
    <el-dialog :visible.sync="isShowVideo" append-to-body>
      <video
        :autoplay="true"
        :src="value"
        controls="controls"
        style="width: 100%;"
        v-if="isShowVideo"
      >
        
      </video>
    </el-dialog>
  </div>
</template>

<script>
import VueHoverMask from "vue-hover-mask";

export default {
  name: "EleUploadVideo",
  components: {
    VueHoverMask,
  },
  props: {
    // 
    value: {
      type: String,
    },
    // 
    action: {
      type: String,
      required: true,
    },
    // 
    responseFn: Function,
    // (Mb)
    fileSize: {
      type: Number,
    },
    // (px)
    width: {
      type: Number,
      default: 360,
    },
    // (auto)
    height: {
      type: Number,
    },
    // 
    isShowTip: {
      type: Boolean,
      default: true,
    },
    // 
    isShowSuccessTip: {
      type: Boolean,
      default: true,
    },
    // 
    fileType: {
      type: Array,
    },
    // ()
    headers: Object,
    //  cookie  ()
    withCredentials: {
      type: Boolean,
      default: false,
    },
    // ()
    data: {
      type: Object,
    },
    //  ()
    name: {
      type: String,
      default: "file",
    },
    //  ()
    httpRequest: Function,
    // thumbnail-mode ()
    accept: String,
    // ()
    beforeRemove: Function,
  },
  data() {
    return {
      isShowVideo: false,
      videoUploadPercent: 0,
    };
  },
  computed: {
    // 
    showTip() {
      return this.isShowTip && (this.fileType || this.fileSize);
    },
  },
  methods: {
    // 
    handleBeforeUploadVideo(file) {
      // 
      let isVideo = false;
      if (Array.isArray(this.fileType)) {
        const type = file.type.split("/");
        isVideo = type[0] === "video" && this.fileType.includes(type[1]);
      } else {
        isVideo = file.type.includes("video");
      }

      if (!isVideo) {
        this.$message.error(`${file.name}, `);
        return false;
      }

      // 
      if (this.fileSize) {
        const isLt = file.size / 1024 / 1024 < this.fileSize;
        if (!isLt) {
          this.$message.error(`${this.fileSize}MB!`);
          return false;
        }
      }
      return true;
    },

    // 
    handleUploadProcess(event, file) {
      this.videoUploadPercent = Number(file.percentage.toFixed(0));
    },

    // 
    handleUploadSuccess(response, file) {
      this.videoUploadPercent = 0;
      if (this.isShowSuccessTip) {
        this.$message.success("!");
      }
      if (this.responseFn) {
        this.$emit("input", this.responseFn(response, file));
      } else {
        this.$emit("input", response);
      }
    },

    // 
    handleUploadError(err, file, fileList) {
      this.$message.error(", !");
      this.videoUploadPercent = 0;
      this.$emit("error", err, file, fileList);
    },

    // 
    doRemove() {
      this.$emit("delete");
      this.$emit("input", null);
    },

    handleRemove() {
      if (!this.beforeRemove) {
        this.doRemove();
      } else if (typeof this.beforeRemove === "function") {
        const before = this.beforeRemove(this.value);
        if (before && before.then) {
          before.then(
            () => {
              this.doRemove();
            },
            () => {}
          );
        } else if (before !== false) {
          this.doRemove();
        }
      }
    },
    // 
    handlePlayerVideo() {
      this.isShowVideo = true;
    },
  },
};
</script>

<style>
.ele-upload-video_mask__item {
  padding: 0 10px;
}

.ele-upload-video .el-upload__tip {
  line-height: 12px;
}
</style>
